a {
  color: var(--link-text);
  text-decoration: none;
}

a:focus {
  box-shadow: 0 0 0 var(--link-focus);
}

a:hover {
  color: var(--link-text);
}

a:hover:not(.button-text, .image, .link-icon, .link-icon-rss, .nav-link, .navbar-brand, .photo) {
  text-decoration: underline;
}

.card {
  margin: auto;
  width: 60%;
}

.card-body {
  background-color: var(--card-background);
  border-color: var(--card-border);
  color: var(--card-text);
  padding-bottom: .5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-top: 1rem;
}

.link-icon {
  &:active, &:hover {
    text-decoration: none;
  }

  &:hover {
    .icon-hover-dark-mode {
      display: none;
    }

    .icon-hover-light-mode {
      display: inline;
    }

    .icon-static-dark-mode {
      display: none;
    }

    .icon-static-light-mode {
      display: none;
    }
  }

  .icon-hover-dark-mode {
    display: none;
  }

  .icon-hover-light-mode {
    display: none;
  }

  .icon-static-dark-mode {
    display: none;
  }

  .icon-static-light-mode {
    display: inline;
  }

  > * {
    height: 3rem;
    width: 3rem;
  }

  padding-left: .625rem;
  padding-right: .625rem;
}

.link-icon-rss {
  &:active, &:hover {
    text-decoration: none;
  }

  &:hover {
    .icon-hover-dark-mode {
      display: none;
    }

    .icon-hover-light-mode {
      display: inline;
    }

    .icon-static-dark-mode {
      display: none;
    }

    .icon-static-light-mode {
      display: none;
    }
  }

  .icon-hover-dark-mode {
    display: none;
  }

  .icon-hover-light-mode {
    display: none;
  }

  .icon-static-dark-mode {
    display: none;
  }

  .icon-static-light-mode {
    display: inline;
  }

  > * {
    width: 2rem;
    height: 2rem;
  }
}

@media screen and (max-width: 48rem) {
  .card {
    margin: auto;
    width: 100%;
  }

  .card-body {
    padding-bottom: .5rem;
    padding-top: 1rem;
  }
}

@media screen and (max-width: 62rem) {
  .card {
    margin: auto;
    width: 75%;
  }

  .card-body {
    padding-bottom: .5rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 1rem;
  }
}

@media (prefers-color-scheme: dark) {
  html {
    .link-icon {
      &:active, &:hover {
        text-decoration: none;
      }

      &:hover {
        .icon-hover-dark-mode {
          display: inline;
        }

        .icon-hover-light-mode {
          display: none;
        }

        .icon-static-dark-mode {
          display: none;
        }

        .icon-static-light-mode {
          display: none;
        }
      }

      .icon-hover-dark-mode {
        display: none;
      }

      .icon-hover-light-mode {
        display: none;
      }

      .icon-static-dark-mode {
        display: inline;
      }

      .icon-static-light-mode {
        display: none;
      }

      > * {
        height: 3rem;
        width: 3rem;
      }

      padding-left: .625rem;
      padding-right: .625rem;
    }
  }

  .link-icon-rss {
    &:active, &:hover {
      text-decoration: none;
    }

    &:hover {
      .icon-hover-dark-mode {
        display: inline;
      }

      .icon-hover-light-mode {
        display: none;
      }

      .icon-static-dark-mode {
        display: none;
      }

      .icon-static-light-mode {
        display: none;
      }
    }

    .icon-hover-dark-mode {
      display: none;
    }

    .icon-hover-light-mode {
      display: none;
    }

    .icon-static-dark-mode {
      display: inline;
    }

    .icon-static-light-mode {
      display: none;
    }

    > * {
      width: 2rem;
      height: 2rem;
    }
  }
}
