html {
  --blockquote-background: #f0efef;
  --blockquote-left-border: #bcbcbd;
  --body-background: #ffffff;
  --body-text: #4c565a;
  --button-background-hover: #001aaf;
  --button-background: #ffffff;
  --button-text-hover: #ffffff;
  --button-text: #4c565a;
  --card-background: #ffffff;
  --card-border: #4c565a;
  --card-text: #4c565a;
  --code-background: #f3f6fa;
  --form-asterisk: #d61b1b;
  --form-input-background: #ffffff;
  --form-placeholder-text: #667175;
  --form-typed-text: #212529;
  --link-focus: #f88e02a6;
  --link-text: #001aaf;
  --navbar-background: #001aaf;
  --navbar-text: #ffffff;
  --page-separator: #e3e4e6;
  --pagination-background-active: #001aaf;
  --pagination-background-hover: #E9ECEF;
  --pagination-background: #ffffff;
  --pagination-text-active: #ffffff;
  --pagination-text: #001aaf;
  --pre-background: #f3f6fa;
  --rss-button: #f88e02;
  --table-border: #dee2e6;
}

// The dark-mode colors must be set after the light-mode colors

@media (prefers-color-scheme: dark) {
  html {
    --blockquote-background: #2c2c2c;
    --body-background: #3c4042;
    --body-text: #cacaca;
    --button-background-hover: #0040ff;
    --button-background: #4c565a;
    --button-text-hover: #cacaca;
    --button-text: #cacaca;
    --card-background: #4c565a;
    --card-border: #cacaca;
    --card-text: #cacaca;
    --code-background: #2c2c2c;
    --form-asterisk: #ff4646;
    --form-input-background: #4c565a;
    --form-placeholder-text: #d3d3d3;
    --form-typed-text: #d3d3d3;
    --link-text: #008cff;
    --page-separator: #bebebe;
    --pagination-background-active: #001aaf;
    --pagination-background-hover: #989898;
    --pagination-background: #4c565a;
    --pagination-text-active: #cacaca;
    --pagination-text: #cacaca;
    --pre-background: #444d51;
    --rss-button: #f8bb02;
  }
}
