.form-button {
  width: 100%;
}

.form-control {
  &:focus {
    background: var(--form-input-background);
    color: var(--form-typed-text);
  }

  background: var(--form-input-background);
  color: var(--form-typed-text);
}

.form-control:-ms-input-placeholder {
  color: var(--form-placeholder-text);
  opacity:1
}

.form-control::-moz-placeholder {
  color: var(--form-placeholder-text);
  opacity:1
}

.form-control::-ms-input-placeholder {
  color: var(--form-placeholder-text);
  opacity:1
}

.form-control::placeholder {
  color: var(--form-placeholder-text);
  opacity: 1;
}

.form-recaptcha {
  padding-bottom: 1rem;
}

.form-required {
  color: var(--form-asterisk);
}

.form-thank-you-button {
  padding-bottom: 1rem;
  padding-top: 2rem;
}

::-webkit-input-placeholder {
  color: var(--form-placeholder-text);
  opacity:1
}
