code {
  background-color: var(--code-background);
  border-radius: .3rem;
  border: solid .0625rem var(--page-separator);
  color: inherit;
  font-family: "Roboto Mono", monospace;
  font-size: 14px;
  padding: .125rem .25rem;
}

// This will make it so that the font size changes with the rest of the HTML container/div around it
header-code {
  background-color: var(--code-background);
  border-radius: .3rem;
  border: solid .0625rem var(--page-separator);
  font-family: "Roboto Mono", monospace;
  padding: 0rem .25rem;
}

pre {
  -webkit-overflow-scrolling: touch;

  > code {
    background: transparent;
    border: 0;
    font-size: 14px;
    margin: 0;
    padding: 0;
    word-break: normal;
    word-wrap: normal;
  }

  background-color: var(--pre-background);
  border-radius: .3rem;
  border: solid .0625rem var(--page-separator);
  font-family: "Roboto Mono", monospace;
  font-size: 14px;
  margin-bottom: 1rem;
  margin-top: 0;
  overflow: auto;
  padding: .8rem;
  width: 100%;
}
