.blog-list-sidebar-icon {
  height: 1.5rem;
  width: 1.5rem;
}

.blog-list-title-icon {
  height: 2.5rem;
  width: 2.5rem;
}

.date-meta {
  color: var(--body-text);
  font-size: 16px;
}

.post-categorization-icon {
  height: 20px;
  width: 20px;
}

.post-data {
  margin-top: -.5rem;
}

.post-meta {
  color: var(--body-text);
  font-size: 16px;
}

.post-subtitle {
  font-size: 32px;
}

.post-summary-data {
  padding-bottom: 2rem;
  padding-left: 0;
  padding-right: 0;
}

.post-summary-title {
  font-size: 20px;
}

.post-title {
  padding-bottom: .75rem;
}

@media (max-width: 782px) {
  .post-subtitle {
    font-size: calc(1rem + 1.5vw);
  }
}
