#anchor a {
  position: absolute;
  top: -3rem;
}

#anchor {
  position: relative;
}

.four-0-four {
  padding-bottom: 2rem;
  padding-top: 1.5rem;
}

.image {
  max-width: 100%;
  padding-bottom: .625rem;
}

.invisible {
  display: none;
}

.italic {
  font-style: italic;
}

.lego-summary {
  font-size: 24px;
  margin-bottom: 10px;
}

.margin-bottom-1 {
  margin-bottom: 1rem;
}

.padding-bottom-1 {
  padding-bottom: 1rem;
}

.page-body {
  padding-top: 1rem;
}

.video {
  max-width: 100%;
}

body {
  align-items: stretch;
  color: var(--body-text);
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "system-ui";
  font-size: 18px;
  height: 100%;
  line-height: 1.5;
}

body, html {
  background-color: var(--body-background);
  color: var(--body-text);
  min-height: 100vh;
  min-width: 20rem;
  position: relative;
  width: 100%;
}

footer {
  border-top: solid .125rem var(--page-separator);
  bottom: 0;
  font-size: 12px;
  left: 0;
  margin-top: 1rem;
  padding-bottom: .7rem;
  padding-top: .7rem;
  text-align: center;
  width: 100%;
}

footer, header, main {
  flex-shrink: 0;
}

h1 {
  border-bottom: solid .125rem var(--page-separator);
  padding-bottom: 1.5rem;
}

h4 {
  font-weight: bold;
}

hr {
  border-top: .2rem solid var(--page-separator);
  margin-bottom: 2rem;
  margin-left: 7.5%; // (100% - width)/2
  margin-top: 2rem;
  opacity: 1;
  width: 85%;
}

main {
  flex-grow: 1;
  font-size: 18px;
  margin-top: 1.5rem;
}
